import React from 'react';
import { APILogin, APILogout, APIEditDB, APIDeleteDB, APIAddDB } from '../API';
import FunctionalitiesMenu from './functionalitiesMenu';

const DBCell = (props) => {
  const {
    addAlert,
    authToken,
    isLoaded,
    setIsLoaded,
    setPanel,
    setPreviousPanel,
    user,
    setUser,
    isWorkingDB,
    setWorkingDB,
  } = props;
  const [db, setDB] = React.useState(props.db);
  const [isEditing, setIsEditing] = React.useState(false);
  const getClassForBtn = () =>
    'float-left select-db btn btn-sm ' +
    (isWorkingDB ? ' btn-success' : ' btn-primary') +
    (isLoaded ? '' : ' btn-disable');
  const getTextForBtn = () => (isWorkingDB ? 'Selected' : 'Select');

  const editStarted = () => {
    setIsEditing(true);
  };

  const onSetWorkingDB = () => {
    setWorkingDB(null);
    setWorkingDB(db);
    addAlert(
      'success',
      'workingDB ' + db.name + ' correctly set.',
      process.env.REACT_APP_ALERT_DEFAULT_DURATION
    );
    setPanel('prospect');
    setPreviousPanel(null);
  };

  const editEnded = () => {
    setIsLoaded(false);
    APIEditDB(
      authToken,
      db,
      (data) => {
        setIsEditing(false);
        setDB(db);
        setUser({
          ...user,
          dbs: user.dbs.map((d) => (d.id === data.id ? data : d)),
        });
      },
      (error) => {
        console.log('Error while editing DB:', error);
        addAlert(
          'danger',
          'Error while editing DB - ' + error,
          process.env.REACT_APP_ALERT_ERROR_DURATION
        );
      },
      () => {
        setIsLoaded(true);
      }
    );
  };

  const onDelete = () => {
    setDB(null);
    // if delete selected database change workingDB in App
    setIsLoaded(false);
    APIDeleteDB(
      authToken,
      db,
      () => {
        //update local data
        if (isWorkingDB) {
          setWorkingDB(null);
        }
        setUser({
          ...user,
          dbs: user.dbs.filter((d) => d.id !== db.id),
        });
      },
      (error) => {
        console.log('error while deleting DB:', error);
        this.addAlert(
          'danger',
          'Error while deleting DB - ' + error,
          process.env.REACT_APP_ALERT_ERROR_DURATION
        );
      },
      () => {
        setIsLoaded(true);
      }
    );
  };

  return db ? (
    <li className='list-group-item p-3'>
      <div className='clearfix'>
        <button
          className={getClassForBtn()}
          onClick={() => {
            onSetWorkingDB();
          }}
          disabled={!isLoaded || isWorkingDB}
        >
          {getTextForBtn()}
        </button>
        {isEditing ? (
          <input
            className='mx-3'
            type='text'
            name='name'
            value={db.name}
            onChange={(event) => {
              let editDB = { ...db };
              editDB[event.target.name] = event.target.value;
              setDB(editDB);
            }}
          />
        ) : (
          <span className='mx-3'>{db.name}</span>
        )}
        <div className='float-right'>
          <FunctionalitiesMenu
            available={isLoaded}
            show={false}
            onEdit={editStarted}
            onEditFinished={editEnded}
            onDeleteConfirmed={onDelete}
            confirmDeleteTimeout={4000}
          />
        </div>
      </div>
    </li>
  ) : null;
};

const AddDBCell = React.forwardRef((props, ref) => {
  const { addAlert, authToken, isLoaded, setIsLoaded, user, setUser } = props;
  const [name, changeName] = React.useState('');
  const [isAdding, setIsAdding] = React.useState(false);

  const addAborted = () => {
    setIsAdding(false);
    changeName('');
  };
  const addEnded = () => {
    setIsLoaded(false);
    APIAddDB(
      authToken,
      { name },
      (data) => {
        setIsAdding(false);
        changeName('');
        //update local data
        setUser({ ...user, dbs: [...user.dbs, data] });
      },
      (error) => {
        console.log('Error occurred while adding category:', error);
        addAlert(
          'danger',
          'Error occurred while adding category - ' + error,
          process.env.REACT_APP_ALERT_ERROR_DURATION
        );
      },
      () => {
        setIsLoaded(true);
      }
    );
  };
  return (
    <div className='w-100 p-3'>
      <div className='m-1 clearfix'>
        {!isAdding && (
          <button
            className='btn btn-success w-100 my-0 py-0'
            onClick={() => {
              setIsAdding(true);
            }}
            disabled={!isLoaded}
          >
            Add new DB
          </button>
        )}
        {isAdding && (
          <div className='form-row'>
            <div className='col-sm mb-2'>
              <input
                type='text'
                className='form-control'
                name='name'
                placeholder='DB name'
                value={name}
                onChange={(event) => {
                  changeName(event.target.value);
                }}
                disabled={!isLoaded}
              />
            </div>
            <div className='col-sm-auto text-center'>
              <button className='btn btn-danger mx-1' onClick={addAborted}>
                Cancel
              </button>
              <button
                className='btn btn-success mx-1'
                onClick={addEnded}
                disabled={!isLoaded}
              >
                Save
              </button>
            </div>
          </div>
        )}
      </div>
    </div>
  );
});

const User = (props) => {
  const {
    addAlert,
    authToken,
    setAuthData,
    isLoaded,
    setIsLoaded,
    setPanel,
    setPreviousPanel,
    user,
    setUser,
    workingDB,
    setWorkingDB,
  } = props;
  const [username, setUsername] = React.useState('');
  const [password, setPassword] = React.useState('');
  const onLogin = () => {
    APILogin(
      username,
      password,
      (data) => {
        addAlert(
          'success',
          'Logged in successfully',
          process.env.REACT_APP_ALERT_ERROR_DURATION
        );
        setAuthData({ token: data.token, uId: data.user_id });
      },
      (error) => {
        let processed_error = error;
        if ('non_field_errors' in error) {
          processed_error = error['non_field_errors'][0];
        }
        addAlert(
          'danger',
          'Error - ' + processed_error,
          process.env.REACT_APP_ALERT_ERROR_DURATION
        );
        console.error('Error while loggin in:', processed_error);
      }
    );
  };
  const onLogout = () => {
    APILogout();
    setAuthData({
      token: null,
      uId: null,
    });
    addAlert(
      'success',
      'User logged out',
      process.env.REACT_APP_ALERT_DEFAULT_DURATION
    );
  };
  return user ? (
    //user logged in
    <React.Fragment>
      <h4>Logged in as {user.username}</h4>
      <div className='text'>
        <h5>Available DBs:</h5>
        {user.dbs.length > 0 && (
          <ul className='list-group'>
            {user.dbs.map((db) => (
              <DBCell
                key={'DB' + db.id}
                db={db}
                isWorkingDB={workingDB ? db.id === workingDB.id : false}
                //
                addAlert={addAlert}
                authToken={authToken}
                isLoaded={isLoaded}
                setIsLoaded={setIsLoaded}
                setPanel={setPanel}
                setPreviousPanel={setPreviousPanel}
                user={user}
                setUser={setUser}
                setWorkingDB={setWorkingDB}
              />
            ))}
          </ul>
        )}
        {user.dbs.length === 0 && (
          <div className='text-center'>
            <p>
              <em>No databases created yet.</em>
            </p>
          </div>
        )}
        <AddDBCell
          addAlert={addAlert}
          authToken={authToken}
          isLoaded={isLoaded}
          setIsLoaded={setIsLoaded}
          user={user}
          setUser={setUser}
        />
      </div>
      <div className='text-center'>
        <button
          className='btn btn-primary'
          onClick={onLogout}
          disabled={!isLoaded}
        >
          Logout
        </button>
      </div>
    </React.Fragment>
  ) : (
    //user not logged in
    <React.Fragment>
      <h4>Please login</h4>
      <form>
        <div className='form-group'>
          <p>
            <label htmlFor='username'>Username/email</label>
            <input
              type='text'
              className='form-control'
              name='username'
              placeholder='username/email'
              value={username}
              onChange={(event) => {
                setUsername(event.target.value);
              }}
              disabled={!isLoaded}
            />
          </p>
          <p>
            <label htmlFor='password'>Password</label>
            <input
              type='password'
              className='form-control'
              name='password'
              placeholder='password'
              value={password}
              onChange={(event) => {
                setPassword(event.target.value);
              }}
              disabled={!isLoaded}
            />
          </p>
        </div>
      </form>
      <div className='text-center'>
        <button
          className='btn btn-primary'
          onClick={onLogin}
          disabled={!isLoaded}
        >
          Login
        </button>
      </div>
    </React.Fragment>
  );
};

export default User;
