import { InlineIcon } from '@iconify/react';
import pencil16 from '@iconify-icons/octicon/pencil-16';
import check16 from '@iconify-icons/octicon/check-16';
import React from 'react';
import { Modal } from 'react-bootstrap';
import { APIAddCash, APIDeleteCash, APIEditCash } from '../API';
import FunctionalitiesMenu from './functionalitiesMenu';
import formatDate from './uniformDate';

const IncomeCell = (props) => {
  const [isEditing, setIsEditing] = React.useState(false);
  const { authToken, incomes, setIncomes, isLoaded, setIsLoaded } = props;
  const [income, setIncome] = React.useState(props.income);
  const eventHandler = (event) => {
    let incomeUpdate = { ...income };
    incomeUpdate[event.target.name] = event.target.value;
    setIncome(incomeUpdate);
  };
  const editStarted = () => {
    setIsEditing(true);
  };
  const editEnded = () => {
    const incomeUpdate = { ...income, name: income.name, value: income.value };
    APIEditCash(
      authToken,
      incomeUpdate,
      (data) => {
        setIsEditing(false);
        setIncomes(incomes.map((c) => (c.id === data.id ? data : c)));
      },
      (error) => {
        console.log('error while editing income', error);
      },
      () => {}
    );
  };
  const onDelete = () => {
    setIsLoaded(false);
    APIDeleteCash(
      authToken,
      income,
      () => {
        //send to parent to handle
        setIncomes(incomes.filter((c) => c.id !== income.id));
      },
      (error) => {
        console.log('error while deleting income', error);
      },
      () => {
        setIsLoaded(true);
      }
    );
  };
  return (
    <li className='list-group-item'>
      <div className='clearfix'>
        <div className='float-right'>
          <FunctionalitiesMenu
            available={isLoaded}
            onEdit={editStarted}
            onEditFinished={editEnded}
            onDeleteConfirmed={onDelete}
            confirmDeleteTimeout={4000}
          />
        </div>
        <div>
          {isEditing ? (
            <input
              type='text'
              name='value'
              value={income.value}
              onChange={eventHandler}
            />
          ) : (
            <span>{income.value}</span>
          )}
          <span> </span>
          <span className='mx-2'></span>
          {isEditing ? (
            <input
              type='text'
              name='name'
              value={income.name}
              onChange={eventHandler}
            />
          ) : (
            <span className='text-muted'>
              <em>{income.name}</em>
            </span>
          )}
        </div>
      </div>
    </li>
  );
};

export const AddIncomeCell = (props) => {
  const [name, changeName] = React.useState('');
  const [value, changeValue] = React.useState('');
  const [isAdding, setIsAdding] = React.useState(false);
  const {
    authToken,
    incomes,
    isLoaded,
    setIsLoaded,
    setIncomes,
    workingMonth,
  } = props;

  const onAdd = () => {
    setIsAdding(true);
  };
  const addAborted = () => {
    setIsAdding(false);
    changeName('');
  };
  const addEnded = () => {
    const workingMonthAndYearExtractor = (wM) => {
      const components = wM.split('-');
      const ret = components.map((c) => parseInt(c));
      return ret;
    };
    const [wMonth, wYear] = workingMonthAndYearExtractor(workingMonth);
    const income = {
      db: localStorage.getItem('workingDBId'),
      name: name,
      value: value,
      income: true,
      reference_date: new Date(wYear, wMonth - 1, 1, 0, 0, 0),
    };
    setIsLoaded(false);
    APIAddCash(
      authToken,
      income,
      (data) => {
        setIsAdding(false);
        changeName('');
        changeValue('');
        setIncomes([...incomes, data]);
      },
      (error) => {
        console.log('Error occurred while registering income', error);
      },
      () => {
        setIsLoaded(true);
      }
    );
  };
  return (
    <div className='w-100 p-2'>
      <div className='m-1 clearfix'>
        {isAdding ? (
          <div>
            <h5>Register new income:</h5>
            <div className='form-row'>
              <div className='col-md mb-2'>
                <div className='form-row'>
                  <div className='col-sm'>
                    <input
                      type='number'
                      step={0.01}
                      className='form-control'
                      name='value'
                      placeholder='Value'
                      value={value}
                      onChange={(event) => {
                        changeValue(event.target.value);
                      }}
                      disabled={!isLoaded}
                    />
                  </div>
                  <div className='col-sm'>
                    <input
                      type='text'
                      className='form-control'
                      name='description'
                      placeholder='Name'
                      value={name}
                      onChange={(event) => {
                        changeName(event.target.value);
                      }}
                      disabled={!isLoaded}
                    />
                  </div>
                </div>
              </div>
              <div className='col-md-auto text-center'>
                <button className='btn btn-danger mx-1' onClick={addAborted}>
                  Cancel
                </button>
                <button
                  className='btn btn-success mx-1'
                  onClick={addEnded}
                  disabled={!isLoaded}
                >
                  Save
                </button>
              </div>
            </div>
          </div>
        ) : (
          <button
            className='btn btn-success w-100 my-0 py-0'
            onClick={() => {
              onAdd();
            }}
            disabled={!isLoaded}
          >
            Register new income
          </button>
        )}
      </div>
    </div>
  );
};

const ActualMoneyOrIncomeModal = (props) => {
  const {
    show,
    //
    authToken,
    incomes,
    setIncomes,
    isLoaded,
    setIsLoaded,
    setShowActualMoneyOrIncomeModal,
    workingDB,
    getWorkingDB,
    workingMonth,
  } = props;

  const [isEditing, setIsEditing] = React.useState(false);
  const [currentMoney, setCurrentMoney] = React.useState(
    workingDB ? workingDB.current_money : null
  );

  const eventHandler = (event) => {
    setCurrentMoney({
      ...currentMoney,
      [event.target.name]: event.target.value,
    });
  };
  const editStarted = () => {
    //console.log(currentMoney);
    setIsEditing(true);
  };
  const editEnded = (callback = () => {}) => {
    const currentMoneyUpdate = {
      ...currentMoney,
      name: 'dummy',
      value: currentMoney.value,
    };
    //console.log('sending this to API', currentMoneyUpdate);
    setIsLoaded(false);
    APIEditCash(
      authToken,
      currentMoneyUpdate,
      (data) => {
        //console.log('received this as data', data);
        setIsEditing(false);
        callback();
        getWorkingDB();
      },
      (error) => {
        console.log('error while editing current_money', error);
      },
      () => {
        setIsLoaded(true);
      }
    );
  };

  return (
    <Modal
      show={show}
      onHide={() => {
        setShowActualMoneyOrIncomeModal(false);
      }}
      size='xl'
    >
      <Modal.Header closeButton>
        <Modal.Title>Add/edit income/actual money</Modal.Title>
      </Modal.Header>
      <Modal.Body>
        <div className='mb-2'>
          <h5>Actual current money:</h5>
          <div className='clearfix'>
            <div className='list-group-item py-3 pr-3 pl-4'>
              <div className='float-right'>
                <button
                  className='btn btn-sm btn-action p-1 pb-2'
                  onClick={
                    isEditing
                      ? () => {
                          editEnded();
                        }
                      : editStarted
                  }
                  disabled={!isLoaded}
                >
                  <InlineIcon icon={isEditing ? check16 : pencil16} />
                </button>
              </div>
              {isEditing ? (
                <input
                  type='text'
                  name='value'
                  value={currentMoney ? currentMoney.value : ''}
                  onChange={eventHandler}
                />
              ) : (
                <span>{currentMoney ? currentMoney.value : 'Loading...'}</span>
              )}
              <span> </span>
              <span className='text-muted'>
                <em>
                  {currentMoney
                    ? isEditing
                      ? ''
                      : ' at ' + formatDate(currentMoney.date)
                    : ''}
                </em>
              </span>
            </div>
          </div>
        </div>
        <h5>
          <div className='clearfix'>
            <div className='float-right'>
              <div className='text-dark'>
                {workingDB ? workingDB.income : ''} 
              </div>
            </div>
            <span>Incomes:</span>
          </div>
        </h5>
        {incomes.length > 0 && (
          <ul className='list-group'>
            {incomes.map((i) => {
              return (
                <IncomeCell
                  authToken={authToken}
                  key={'income' + i.id}
                  income={i}
                  incomes={incomes}
                  setIncomes={setIncomes}
                  isLoaded={isLoaded}
                  setIsLoaded={setIsLoaded}
                />
              );
            })}
          </ul>
        )}
        {incomes.length === 0 && (
          <p className='text-center'>
            <em>No incomes registered yet.</em>
          </p>
        )}
        <AddIncomeCell
          authToken={authToken}
          incomes={incomes}
          setIncomes={setIncomes}
          isLoaded={isLoaded}
          setIsLoaded={setIsLoaded}
          workingMonth={workingMonth}
        />
      </Modal.Body>
      {isEditing && (
        <Modal.Footer>
          <div className='text-center w-100'>
            <button
              className={'btn btn-primary px-5'}
              onClick={() => {
                editEnded(() => {
                  setShowActualMoneyOrIncomeModal(false);
                });
              }}
            >
              {'Save and close'}
            </button>
          </div>
        </Modal.Footer>
      )}
    </Modal>
  );
};
export default ActualMoneyOrIncomeModal;
