import React from 'react';
import { InlineIcon } from '@iconify/react';
import workflow16 from '@iconify-icons/octicon/workflow-16';
import FunctionalitiesMenu from '../functionalitiesMenu';
import { useSwipeable } from 'react-swipeable';
import {
  APIAddCategory,
  APIEditCategory,
  APIDeleteCategory,
  APIDeleteExpenditure,
  APICopyPrecMonthExp,
} from '../../API';

import formatDate from '../uniformDate';

const CategoryCell = (props) => {
  const {
    addAlert,
    authToken,
    isLoaded,
    setIsLoaded,
    workingDB,
    setWorkingDB,
    showEditFunctions,
  } = props;
  const [category, setCategory] = React.useState(props.category);
  const [isEditing, setIsEditing] = React.useState(false);
  const eventHandler = (event) => {
    let categoryUpdate = { ...category };
    categoryUpdate[event.target.name] = event.target.value;
    setCategory(categoryUpdate);
  };
  const editStarted = () => {
    setIsEditing(true);
  };
  const editEnded = () => {
    setIsLoaded(false);
    APIEditCategory(
      authToken,
      category,
      (data) => {
        setIsEditing(false);
        setWorkingDB({
          ...workingDB,
          categories: workingDB.categories.map((c) =>
            c.id === data.id ? data : c
          ),
        });
      },
      (error) => {
        console.log('error while editing category', error);
        addAlert(
          'danger',
          'Error while editing category - ' + error,
          process.env.REACT_APP_ALERT_ERROR_DURATION
        );
      },
      () => {
        setIsLoaded(true);
      }
    );
  };
  const onDelete = () => {
    setIsLoaded(false);
    APIDeleteCategory(
      authToken,
      category,
      (data) => {
        setWorkingDB({
          ...workingDB,
          categories: workingDB.categories.filter((c) => category.id !== c.id),
        });
      },
      (error) => {
        console.log('error while deleting category', error);
        addAlert(
          'danger',
          'Error while editing deleting - ' + error,
          process.env.REACT_APP_ALERT_ERROR_DURATION
        );
      },
      () => {
        setIsLoaded(true);
      }
    );
  };

  return (
    category && (
      <div className='w-100 pl-1'>
        <div className='text-primary m-1 clearfix'>
          {showEditFunctions && isEditing ? (
            <input
              type='text'
              name='name'
              value={category.name}
              onChange={eventHandler}
            />
          ) : (
            <span>{category.name}</span>
          )}
          {showEditFunctions ? (
            <div className='float-right'>
              <FunctionalitiesMenu
                available={isLoaded}
                onEdit={editStarted}
                onEditFinished={editEnded}
                onDeleteConfirmed={onDelete}
                confirmDeleteTimeout={4000}
              />
            </div>
          ) : null}
        </div>
      </div>
    )
  );
};
export const AddCategoryCell = (props) => {
  const [name, changeName] = React.useState('');
  const [isAdding, setIsAdding] = React.useState(false);
  const {
    addAlert,
    authToken,
    isLoaded,
    setIsLoaded,
    workingDB,
    setWorkingDB,
  } = props;

  const onAdd = () => {
    setIsAdding(true);
  };
  const addAborted = () => {
    setIsAdding(false);
    changeName('');
  };
  const addEnded = () => {
    const category = {
      db: workingDB.id,
      name: name,
    };
    setIsLoaded(false);
    APIAddCategory(
      authToken,
      category,
      (data) => {
        setIsAdding(false);
        changeName('');
        //updateLocalData();
        setWorkingDB({
          ...workingDB,
          categories: [...workingDB.categories, data],
        });
      },
      (error) => {
        console.log('Error occurred while adding category', error);
        addAlert(
          'danger',
          'Error occurred while adding category - ' + error,
          process.env.REACT_APP_ALERT_ERROR_DURATION
        );
      },
      () => {
        setIsLoaded(true);
      }
    );
  };
  return (
    <div className='w-100 p-2'>
      <div className='m-1 clearfix'>
        {isAdding && (
          <div className='form-row'>
            <div className='col-sm mb-2'>
              <input
                type='text'
                className='form-control'
                name='name'
                placeholder='Category name'
                value={name}
                onChange={(event) => {
                  changeName(event.target.value);
                }}
                disabled={!isLoaded}
              />
            </div>
            <div className='col-sm-auto text-center'>
              <button className='btn btn-danger mx-1' onClick={addAborted}>
                Cancel
              </button>
              <button
                className='btn btn-success mx-1'
                onClick={addEnded}
                disabled={!isLoaded}
              >
                Save
              </button>
            </div>
          </div>
        )}
        {!isAdding && (
          <button
            className='btn btn-success w-100 my-0 py-0'
            onClick={() => {
              onAdd();
            }}
            disabled={!isLoaded}
          >
            Add new category
          </button>
        )}
      </div>
    </div>
  );
};
const ExpenditureCell = (props) => {
  const hasMouse = window.matchMedia('(hover:hover').matches;
  const [showControls, setShowControls] = React.useState(hasMouse);
  const [expenditure, setExpenditure] = React.useState(props.expenditure);
  const {
    addAlert,
    authToken,
    expenditures,
    setExpenditures,
    isLoaded,
    setIsLoaded,
    setExpenditureForModal,
    setShowExpenditureModal,
  } = props;

  React.useEffect(() => {
    setExpenditure(props.expenditure);
  }, [props.expenditure]);

  const onEdit = (expenditure) => {
    setExpenditureForModal(expenditure);
    setShowExpenditureModal(true);
  };
  const onDelete = (expenditure) => {
    setIsLoaded(false);
    APIDeleteExpenditure(
      authToken,
      expenditure,
      () => {
        setExpenditures(expenditures.filter((e) => e.id !== expenditure.id));
      },
      (error) => {
        console.log('Error while deleting expenditure', error);
        addAlert(
          'danger',
          'Error while deleting expenditure - ' + error,
          process.env.REACT_APP_ALERT_ERROR_DURATION
        );
      },
      () => {
        setIsLoaded(true);
      }
    );
  };
  const handlers = useSwipeable({
    onSwipedLeft: () => {
      setShowControls(true);
    },
    onSwipedRight: () => {
      setShowControls(false);
    },
    preventDefaultTouchmoveEvent: true,
    trackMouse: true,
  });

  return expenditure ? (
    <div className='pl-3 pr-1 py-1'>
      <div
        {...(hasMouse ? null : handlers)}
        className='d-flex align-items-stretch bg-light border border-dark rounded m-0'
      >
        <div className='flex-grow-1 pl-1'>
          <div className='text'>{expenditure.name}</div>
          <div className='text text-muted'>{formatDate(expenditure.date)}</div>
          <div className='text'>
            <span>{expenditure.value + ' '}</span>
          </div>
        </div>
        <div className='d-flex flex-column'>
          <div className='flex-grow-1 mt-1 mr-1'>
            <FunctionalitiesMenu
              showExpander={hasMouse}
              showControls={!hasMouse && showControls}
              onShowControlsChanged={(showC) => {
                setShowControls(showC);
              }}
              available={isLoaded}
              onEdit={
                hasMouse
                  ? (e) => {
                      e.currentTarget.blur();
                      onEdit(expenditure);
                    }
                  : () => {
                      setShowControls(false);
                      onEdit(expenditure);
                    }
              }
              onDeleteConfirmed={() => {
                onDelete(expenditure);
              }}
              confirmDeleteTimeout={4000}
            />
          </div>
          {(Boolean(expenditure.expected_expenditure) ||
            (expenditure.is_expected &&
              expenditure.actual_expenditures.length > 0)) && (
            <div className='clearfix'>
              <div className='float-right mb-1 mr-1'>
                <button className='btn btn-small btn-action'>
                  <InlineIcon icon={workflow16} rotate={135} />
                </button>
              </div>
            </div>
          )}
        </div>
      </div>
    </div>
  ) : null;
};

const CategoriesView = (props) => {
  const {
    selectedPanel,
    //
    addAlert,
    authToken,
    expenditures,
    setExpenditures,
    isLoaded,
    onRefresh,
    setIsLoaded,
    workingDB,
    setWorkingDB,
    workingMonth,
    setExpenditureForModal,
    setShowExpenditureModal,
  } = props;
  const filterExpenditure = (expenditure) => {
    if (!workingMonth) {
      return false;
    }
    let [month, year] = workingMonth.split('-');
    month = parseInt(month);
    year = parseInt(year);
    const d = new Date(expenditure.date);
    const ret = month === d.getMonth() + 1 && year === d.getFullYear();
    return ret;
  };
  return (
    <React.Fragment>
      {workingDB.categories.length === 0 && (
        <div className='text text-center'>
          <p>
            <em>No categories created yet.</em>
          </p>
        </div>
      )}
      {workingDB.categories.length > 0 &&
        workingDB.categories.map((category) => {
          let expendituresToRender = [];
          if (expenditures) {
            expendituresToRender = expenditures.filter(
              (e) =>
                e.is_expected === (selectedPanel === 'expected') &&
                e.category === category.id &&
                filterExpenditure(e)
            );
          }
          return (
            <div
              key={'category' + selectedPanel + category.id}
              className='border border-primary rounded my-2'
            >
              <CategoryCell
                category={category}
                showEditFunctions={true}
                //
                addAlert={addAlert}
                authToken={authToken}
                isLoaded={isLoaded}
                setIsLoaded={setIsLoaded}
                workingDB={workingDB}
                setWorkingDB={setWorkingDB}
              />
              {expendituresToRender.length > 0 ? (
                expendituresToRender.map((expenditure) => (
                  <ExpenditureCell
                    key={
                      'expenditure' +
                      expenditure.category +
                      selectedPanel +
                      expenditure.id
                    }
                    expenditure={expenditure}
                    addAlert={addAlert}
                    authToken={authToken}
                    expenditures={expenditures}
                    setExpenditures={setExpenditures}
                    isLoaded={isLoaded}
                    setIsLoaded={setIsLoaded}
                    workingDB={workingDB}
                    setWorkingDB={setWorkingDB}
                    setExpenditureForModal={setExpenditureForModal}
                    setShowExpenditureModal={setShowExpenditureModal}
                  />
                ))
              ) : (
                <p className='text-center'>
                  <em>No expenditures added yet.</em>
                </p>
              )}
              <div className='m-1 clearfix' style={{ minHeight: 10 }}>
                {expendituresToRender.length > 0 && (
                  <>
                    <div className='float-left'>
                      {expendituresToRender.length +
                        (expendituresToRender.length !== 1
                          ? ' expenditures'
                          : ' expenditure')}
                    </div>
                    <div className='float-right'>
                      {selectedPanel === 'actual'
                        ? category.actual_expenditure
                        : category.expected_expenditure}{' '}
                      
                    </div>
                  </>
                )}
              </div>
            </div>
          );
        })}
      <AddCategoryCell
        addAlert={addAlert}
        authToken={authToken}
        isLoaded={isLoaded}
        setIsLoaded={setIsLoaded}
        workingDB={workingDB}
        setWorkingDB={setWorkingDB}
      />
      <div className='w-100 p-2'>
        <button
          className='btn btn-primary w-100 my-0 py-0'
          onClick={(e) => {
            APICopyPrecMonthExp(
              authToken,
              workingDB.id,
              workingMonth,
              {},
              () => {},
              () => {},
              () => {
                onRefresh();
              }
            );
          }}
        >
          Copy precend months expected expenditures
        </button>
      </div>
    </React.Fragment>
  );
};

export default CategoriesView;
