import React from 'react';
import { formatMonth } from '../uniformDate';

const MonthCell = (props) => {
  const { month, isWorkingMonth, isLoaded, setWorkingMonth } = props;
  const [monthName, monthIncome, monthActualMoney] = month;
  return (
    <li className='list-group-item p-3'>
      <div className='row'>
        <div className='col-auto px-1'>
          <button
            className={
              'px-2 py-1 btn btn-sm ' +
              (isWorkingMonth ? 'btn-success' : 'btn-primary')
            }
            disabled={!isLoaded || isWorkingMonth}
            onClick={() => {
              setWorkingMonth(monthName);
            }}
          >
            {/*isWorkingMonth ? 'Selected' : 'Select'*/ 'Select'}
          </button>
        </div>
        <div className='col px-1'>{formatMonth(monthName)}</div>
        <div className='col px-1'>{monthIncome}</div>
        <div className='col px-1'>{monthActualMoney}</div>
      </div>
    </li>
  );
};

const Months = (props) => {
  const { isLoaded, months, workingMonth, setWorkingMonth } = props;
  const [showExplanation, setShowExaplanation] = React.useState(false);
  return (
    <div>
      {months.length === 0 && (
        <p className='text-center'>
          <em>No months to show.</em>
        </p>
      )}
      {months.length > 0 && (
        <ul className='list-group'>
          {months.map((month) => {
            return (
              <MonthCell
                key={month}
                month={month}
                isWorkingMonth={workingMonth === month[0]}
                //
                isLoaded={isLoaded}
                setWorkingMonth={setWorkingMonth}
              />
            );
          })}
        </ul>
      )}
      <p className='text-center'>
        <em>
          {showExplanation &&
            'Months will appear here if they have at least one expenditure or an income registered. '}
          <a
            href='#dummy'
            onClick={(e) => {
              e.preventDefault();
              setShowExaplanation(!showExplanation);
            }}
          >
            {showExplanation ? 'Hide' : 'How do I add months?'}
          </a>
        </em>
      </p>
    </div>
  );
};

export default Months;
