import React from 'react';
import './CSSTransitionClasses.css';
import './App.css';
import Alerts from './components/alerts';
import UpperBar from './components/upperBar';
import BottomBar from './components/bottomBar';
import User from './components/user';
import Prospect from './components/mainViewComponents/prospect';
import CategoriesView from './components/mainViewComponents/expenditures';
import Months from './components/mainViewComponents/months';
import AddEditExpenditureModal from './components/addEditExpenditureModal';
import ActualMoneyOrIncomeModal from './components/actualMoneyOrIncomeModal';
import {
  APIGetCashes,
  APIGetDB,
  APIGetExpenditures,
  APIGetMonths,
  APIGetUser,
} from './API';
import loading from './media/img/loading256.gif';
import { useObjectsDidUpdateEffect } from './customHooks';

const getCurrentMonth = () => {
  const d = new Date();
  const month = d.getMonth() + 1;
  const year = d.getFullYear();
  const currentMonth = (month < 10 ? '0' + month : '' + month) + '-' + year;
  return currentMonth;
};

const App = (props) => {
  const [authData, setAuthData] = React.useState({
    token: localStorage.getItem('authToken'),
    uId: localStorage.getItem('userId'),
  });
  const [user, setUser] = React.useState(null);
  const [workingDB, setWorkingDB] = React.useState(null);
  const [workingMonth, setWorkingMonth] = React.useState(getCurrentMonth());
  const [expenditures, setExpenditures] = React.useState(null);
  const [incomes, setIncomes] = React.useState(null);
  const [months, setMonths] = React.useState(null);
  const [isLoaded, setIsLoaded] = React.useState(false);
  const [alerts, setAlerts] = React.useState([]);
  const [selectedPanel, setSelectedPanel] = React.useState('prospect');
  const [previousPanel, setPreviousPanel] = React.useState(null);
  const [showCashModal, setShowActualMoneyOrIncomeModal] = React.useState(
    false
  );
  const [showExpenditureModal, setShowExpenditureModal] = React.useState(false);
  const [
    expenditureModalIsExpected,
    setExpenditureModalIsExpected,
  ] = React.useState(false);
  const [expenditureForModal, setExpenditureForModal] = React.useState(null);

  const addAlert = (type, body, autodismiss = 0) => {
    const getAlertId = () => {
      const d = new Date();
      return d.getTime();
    };
    let alert = { type: type, body: body, id: getAlertId() };
    if (autodismiss > 0) {
      alert['autodismissFunction'] = () => {
        setAlerts(alerts.filter((a) => a.id !== alert.id));
      };
      alert['autodismissTimeout'] = setTimeout(
        alert['autodismissFunction'],
        autodismiss
      );
    }
    setAlerts([...alerts, alert]);
  };

  const setPanel = (target = null, setPrevious = false) => {
    if (target !== selectedPanel) {
      if (setPrevious) {
        setPreviousPanel(
          selectedPanel && selectedPanel !== target ? selectedPanel : 'prospect'
        );
      } else {
        setPreviousPanel(null);
      }
      setSelectedPanel(target ? target : 'prospect');
    }
  };

  const back = () => {
    if (previousPanel) {
      setPanel(previousPanel);
      setPreviousPanel(null);
    }
  };

  const getCurrentUser = () => {
    //changeIsLoaded(false, 'getCurrentUser')
    if (
      authData.uId &&
      authData.token &&
      authData.uId !== 'null' &&
      authData.token !== 'null'
    ) {
      APIGetUser(
        authData.token,
        authData.uId,
        workingMonth,
        (data) => {
          //console.log('user SET');
          setUser(data);
        },
        (error) => {
          const mex = Object.keys(error).reduce((prev, k) => {
            return prev + k + ':' + error[k];
          }, '');
          console.error('Error while getting user', mex);
          addAlert(
            'danger',
            'Error while getting current user:' + mex,
            process.env.REACT_APP_ALERT_ERROR_DURATION
          );
          setPanel('user');
        },
        () => {
          setIsLoaded(true);
        }
      );
    } else {
      //console.log('not authData');
      setIsLoaded(true);
      setUser(null);
      setPanel('user');
    }
  };
  const getWorkingDB = () => {
    //console.log('getWorkingDB');
    const workingDBId = localStorage.getItem('workingDBId');
    if (user && (workingDBId || user.dbs.length === 1)) {
      //automatically set working DB
      setIsLoaded(false);
      APIGetDB(
        authData.token,
        workingDBId ? workingDBId : user.dbs[0].id,
        workingMonth,
        {},
        (data) => {
          //console.log('workingDBSET');
          //console.log(data);
          setWorkingDB(data);
        },
        (error) => {
          //handle errors here
          //console.log('error gettin working db', error);
          localStorage.removeItem('workingDBId');
        },
        () => {
          setIsLoaded(true);
        }
      );
    } else {
      //console.log('not user && (workingDBId || user.dbs.length === 1)');
      setIsLoaded(true);
      setWorkingDB(null);
      setIncomes(null);
      setExpenditures(null);
      setPanel('user');
    }
  };
  const getExpenditures = () => {
    //console.log('getExpenditures');
    if (workingDB) {
      setIsLoaded(false);
      APIGetExpenditures(
        authData.token,
        workingDB.id,
        workingMonth,
        {},
        (data) => {
          //console.log('Expenditures set');
          setExpenditures(data.results);
        },
        (error) => {
          //console.log('error gettin expenditures', error);
        },
        () => {
          setIsLoaded(true);
        }
      );
    } else {
      //console.log('no expenditures because no workingDB');
      setExpenditures(null);
    }
  };
  const getIncomes = () => {
    //console.log('getIncomes');
    if (workingDB) {
      setIsLoaded(false);
      APIGetCashes(
        authData.token,
        workingDB.id,
        workingMonth,
        {},
        (data) => {
          //console.log('incomes set');
          //console.log(data.results);
          setIncomes(data.results.filter((obj) => obj.income));
        },
        (error) => {
          //console.log('error while gettin incomes', error);
        },
        () => {
          setIsLoaded(true);
        }
      );
    } else {
      //console.log('no expenditures because no workingDB');
      setIncomes(null);
    }
  };
  const getMonths = () => {
    //console.log('getMonths');
    if (workingDB) {
      setIsLoaded(false);
      APIGetMonths(
        authData.token,
        workingDB.id,
        (data) => {
          //console.log('months set');
          setMonths(data.results);
        },
        (error) => {
          //console.log('error while gettin month', error);
        },
        () => {
          setIsLoaded(true);
        }
      );
    } else {
      //console.log('no expenditures because no workingDB');
      setMonths(null);
    }
  };

  useObjectsDidUpdateEffect(
    () => {
      localStorage.setItem('authToken', authData.token);
      localStorage.setItem('userId', authData.uId);
    },
    [authData],
    (prevObj, obj, firstCall) => !firstCall
  );

  useObjectsDidUpdateEffect(
    () => {
      localStorage.setItem('workingDBId', workingDB.id);
    },
    [workingDB],
    (prevObj, obj, firstCall) => !firstCall && obj
  );

  React.useEffect(() => {
    //get current user everytime authData changes
    getCurrentUser();
  }, [authData]);

  useObjectsDidUpdateEffect(
    /* setWorkingDB, expenditures, incomeses, months = null if user is null */
    () => {
      getWorkingDB();
    },
    [user],
    (prevObj, obj, firstCall) => !firstCall,
    (prevObjs, objs, firstCalls) => !objs[0]
  );

  useObjectsDidUpdateEffect(
    /* get Working DB when user or workingMonth changes but:
      - not at their first render
      - if user is not null
    */
    getWorkingDB,
    [user],
    (prevObj, obj, firstCall) => !firstCall,
    (prevObjs, objs, firstCalls) => objs[0]
  );

  useObjectsDidUpdateEffect(
    /* update incomes, expenditures, months when workingDB changes but:
      - not at its first render
      - only if it changes from null or to null
    */
    () => {
      getIncomes();
      getExpenditures();
      getMonths();
      setPanel('prospect');
    },
    [workingDB],
    (prevObj, obj, firstCall) => !firstCall && (!prevObj || !obj)
  );

  useObjectsDidUpdateEffect(
    /* update incomes, expenditures, months when workingMonth changes but:
      - not at its first render
      - only if it changes from null or to null
    */
    () => {
      getIncomes();
      getExpenditures();
      setPanel('prospect');
    },
    [workingMonth],
    (prevObj, obj, firstCall) => !firstCall && prevObj !== obj
  );

  useObjectsDidUpdateEffect(
    /* get workingDB, months when expenditures or incomes change but:
      - not at their first render
      - not if their previous value was null
    */
    () => {
      getWorkingDB();
      getMonths();
    },
    [incomes, expenditures],
    (prevObj, obj, firstCall) => !firstCall,
    (prevObjs, objs, firstCalls) => prevObjs[0] && prevObjs[1]
  );

  const refresh = () => {
    getCurrentUser();
    getIncomes();
    getExpenditures();
  };

  /*
  FOR TEST PURPOSES
  
  const userSetNullRef = React.useRef(false);
  if (!userSetNullRef.current) {
    setTimeout(() => {
      //console.log('set user null');
      setUser(null);
    }, 5000);
    userSetNullRef.current = true;
  }*/

  return (
    <React.Fragment>
      <UpperBar
        isLoaded={isLoaded}
        onBack={back}
        onRefresh={refresh}
        previousPanel={previousPanel}
        selectedPanel={selectedPanel}
        setExpenditureForModal={setExpenditureForModal}
        setExpenditureModalIsExpected={setExpenditureModalIsExpected}
        setPanel={setPanel}
        setShowCashModal={setShowActualMoneyOrIncomeModal}
        setShowExpenditureModal={setShowExpenditureModal}
        setWorkingMonth={setWorkingMonth}
        user={user}
        workingDB={workingDB}
      />
      <Alerts
        alerts={alerts}
        onClose={(alert) => {
          clearTimeout(alert.autodismissTimeout);
          alert.autodismissFunction();
        }}
      />
      {selectedPanel === 'user' && (
        <User
          addAlert={addAlert}
          authToken={authData.token}
          setAuthData={setAuthData}
          isLoaded={isLoaded}
          onBack={back}
          setIsLoaded={setIsLoaded}
          setUser={setUser}
          setPanel={setPanel}
          setPreviousPanel={setPreviousPanel}
          workingDB={workingDB}
          setWorkingDB={setWorkingDB}
          user={user}
        />
      )}
      {selectedPanel !== 'user' && (
        <div className='safe-down'>
          {!workingDB && (
            <div className='text-center'>
              <img src={loading} alt='Loading...' style={{ maxWidth: 150 }} />
            </div>
          )}
          {workingDB && (
            <div>
              {selectedPanel === 'prospect' && (
                <Prospect workingDB={workingDB} />
              )}
              {(selectedPanel === 'actual' || selectedPanel === 'expected') && (
                <CategoriesView
                  selectedPanel={selectedPanel}
                  //
                  addAlert={addAlert}
                  authToken={authData.token}
                  expenditures={expenditures}
                  setExpenditures={setExpenditures}
                  isLoaded={isLoaded}
                  onRefresh={refresh}
                  setIsLoaded={setIsLoaded}
                  workingDB={workingDB}
                  setWorkingDB={setWorkingDB}
                  workingMonth={workingMonth}
                  setExpenditureForModal={setExpenditureForModal}
                  setShowExpenditureModal={setShowExpenditureModal}
                />
              )}
              {selectedPanel === 'months' && (
                <Months
                  isLoaded={isLoaded}
                  months={months}
                  workingMonth={workingMonth}
                  setWorkingMonth={setWorkingMonth}
                />
              )}
              <BottomBar
                isLoaded={isLoaded}
                selectedPanel={selectedPanel}
                setPanel={setPanel}
              />
            </div>
          )}
        </div>
      )}

      {showExpenditureModal && (
        <AddEditExpenditureModal
          show={showExpenditureModal}
          expenditure={expenditureForModal}
          isExpected={expenditureModalIsExpected}
          //
          authToken={authData.token}
          expenditures={expenditures}
          setExpenditures={setExpenditures}
          isLoaded={isLoaded}
          setIsLoaded={setIsLoaded}
          setShowExpenditureModal={setShowExpenditureModal}
          workingDB={workingDB}
        />
      )}
      {showCashModal && (
        <ActualMoneyOrIncomeModal
          show={showCashModal}
          //
          authToken={authData.token}
          incomes={incomes}
          setIncomes={setIncomes}
          isLoaded={isLoaded}
          setIsLoaded={setIsLoaded}
          setShowActualMoneyOrIncomeModal={setShowActualMoneyOrIncomeModal}
          workingDB={workingDB}
          getWorkingDB={getWorkingDB}
          setWorkingDB={setWorkingDB}
          workingMonth={workingMonth}
        />
      )}
    </React.Fragment>
  );
};

export default App;
