import React from 'react';
import RefreshBtn from './upperBarComponents/refreshBtn';
import UserBtn from './upperBarComponents/userBtn';
import UpperRightBtn from './upperBarComponents/upperRightBtn';
import BackBtn from './upperBarComponents/backBtn';

const UpperBar = (props) => {
  const {
    isLoaded,
    onBack,
    onRefresh,
    previousPanel,
    selectedPanel,
    setExpenditureForModal,
    setExpenditureModalIsExpected,
    setPanel,
    setShowCashModal,
    setShowExpenditureModal,
    setWorkingMonth,
    user,
    workingDB,
  } = props;
  return (
    <div className='fixed-top bg-white safe-fixed-x safe-fixed-top'>
      <div className='row m-0 p-1'>
        <div className='col-9 text-left p-0'>
          <RefreshBtn isLoaded={isLoaded} onRefresh={onRefresh} />
          {previousPanel && <BackBtn isLoaded={isLoaded} onBack={onBack} />}
          <UserBtn
            isLoaded={isLoaded}
            setPanel={setPanel}
            user={user}
            workingDB={workingDB}
          />
        </div>
        <div className='col-3 text-right p-0'>
          <UpperRightBtn
            isLoaded={isLoaded}
            selectedPanel={selectedPanel}
            setExpenditureForModal={setExpenditureForModal}
            setExpenditureModalIsExpected={setExpenditureModalIsExpected}
            setShowCashModal={setShowCashModal}
            setShowExpenditureModal={setShowExpenditureModal}
            setWorkingMonth={setWorkingMonth}
          />
        </div>
      </div>
    </div>
  );
};

export default UpperBar;
