import React from 'react';
/*
  Evaluate objects with passed functions:
    evalutating Function:
      - is applied by some method, so the conditions are satisfied when at least one object
        passed to evaluatingFunction makes it return true
      - default is obj !== prevObj
    multipleEvaluatingFunction:
      - takes all objects as argument so you can apply specific condition knowing
        array sort order (which is the same as objects)
      - default is always true
    the results of these two evaluating functions are combined with the logical operator &&
*/
export const useObjectsDidUpdateEffect = (
  effect,
  objects,
  evaluatingFunction = (prevObj, obj, firstCall) => obj !== prevObj,
  multipleEvaluatingFunction = (prevObjs, objs, firstCalls) => true
) => {
  let fcs = [];
  const objectsRef = React.useRef(
    objects.map((o) => {
      fcs.push(true);
      return o;
    })
  );
  const firstCalls = React.useRef(fcs);
  React.useEffect(() => {
    if (
      objects.some((o, i) =>
        evaluatingFunction(objectsRef.current[i], o, firstCalls.current[i])
      ) &&
      multipleEvaluatingFunction(
        objectsRef.current,
        objects,
        firstCalls.current
      )
    ) {
      effect();
    }
    objects.forEach((o, i) => {
      objectsRef.current[i] = o;
      if (firstCalls.current[i]) {
        firstCalls.current[i] = false;
      }
    });
  }, objects);
};
