import React from 'react';
import { InlineIcon } from '@iconify/react';
import pencil16 from '@iconify-icons/octicon/pencil-16';
import plusCircle16 from '@iconify-icons/octicon/plus-circle-16';
import calendar16 from '@iconify-icons/octicon/calendar-16';

const UpperRightBtn = (props) => {
  const {
    isLoaded,
    selectedPanel,
    setExpenditureForModal,
    setExpenditureModalIsExpected,
    setShowCashModal,
    setShowExpenditureModal,
    setWorkingMonth,
  } = props;
  const getOnClick = () => {
    switch (selectedPanel) {
      case 'prospect':
        return (e) => {
          setShowCashModal(true);
        };
      case 'months':
        return (e) => {
          const d = new Date();
          const month = d.getMonth() + 1;
          const year = d.getFullYear();
          setWorkingMonth((month < 10 ? '0' + month : '' + month) + '-' + year);
        };
      case 'expected':
        return (e) => {
          e.currentTarget.blur();
          setExpenditureForModal(null);
          setExpenditureModalIsExpected(true);
          setShowExpenditureModal(true);
        };
      case 'actual':
        return (e) => {
          e.currentTarget.blur();
          setExpenditureForModal(null);
          setExpenditureModalIsExpected(false);
          setShowExpenditureModal(true);
        };
      default:
        return () => {};
    }
  };
  const getIcon = () => {
    switch (selectedPanel) {
      case 'prospect':
        return <InlineIcon icon={pencil16} />;
      case 'months':
        return <InlineIcon icon={calendar16} />;
      default:
        //
        return <InlineIcon icon={plusCircle16} />;
    }
  };
  return (
    selectedPanel !== 'user' && (
      <button
        className='btn btn-outline-primary'
        disabled={!isLoaded}
        onClick={getOnClick()}
      >
        {getIcon()}
      </button>
    )
  );
};

export default UpperRightBtn;
