import React from 'react';

const BottomButton = (props) => {
  const { selectedPanel, setPanel, isLoaded, button } = props;
  return (
    <div className='col text-center pl-0 pr-0'>
      <button
        className={
          'btn btn-sm w-100 ' +
          (selectedPanel === button.name
            ? 'btn-secondary'
            : 'btn-outline-secondary')
        }
        onClick={() => {
          setPanel(button.name);
        }}
        disabled={!isLoaded}
      >
        {button.display}
      </button>
    </div>
  );
};

const BottomBar = (props) => {
  const buttons = [
    {
      name: 'prospect',
      display: 'Prospect',
    },
    {
      name: 'actual',
      display: 'Actual',
    },
    {
      name: 'expected',
      display: 'Expected',
    },
    {
      name: 'months',
      display: 'Months',
    },
  ];
  const { isLoaded, selectedPanel, setPanel } = props;
  return (
    <footer className='footer fixed-bottom bg-white safe-fixed-x safe-fixed-bottom'>
      <div className='row pt-1'>
        <div className='col pr-0' style={{ maxWidth: 2 }}></div>
        {buttons.map((button, idx) => (
          <BottomButton
            key={'button' + idx}
            selectedPanel={selectedPanel}
            setPanel={setPanel}
            isLoaded={isLoaded}
            button={button}
          />
        ))}
        <div className='col pl-0' style={{ maxWidth: 2 }}></div>
      </div>
    </footer>
  );
};

export default BottomBar;
