import React from 'react';
import { Modal } from 'react-bootstrap';
import { APIAddExpenditure, APIEditExpenditure } from '../API';

const AddEditExpenditureModal = (props) => {
  const {
    isExpected,
    authToken,
    expenditures,
    setExpenditures,
    isLoaded,
    setIsLoaded,
    setShowExpenditureModal,
    workingDB,
  } = props;
  const dateToLocaleISOString = (date = new Date()) => {
    const utcMilliseconds = date.getTime();
    const localMilliseconds =
      utcMilliseconds - date.getTimezoneOffset() * 60 * 1000;
    const localDate = new Date(localMilliseconds);
    return localDate.toISOString().slice(0, -8);
  };

  const prepareExpenditureToBeSavedLocally = (exp) => {
    let ret = { ...exp };
    ret['category'] = ret['category'] ? ret['category'] : 0;
    ret['expected_expenditure'] = ret['expected_expenditure']
      ? ret['expected_expenditure']
      : 0;
    ret['date'] = ret.date.slice(0, -9);
    return ret;
  };

  const [expenditure, setExpenditure] = React.useState(
    props.expenditure
      ? prepareExpenditureToBeSavedLocally(props.expenditure)
      : {
          name: '',
          value: '',
          date: dateToLocaleISOString(),
          category: 0,
          is_expected: isExpected,
          expected_expenditure: 0,
          db: workingDB.id,
        }
  );
  const handleChange = (event) => {
    const { name, value } = event.target;
    let expenditure_update = { ...expenditure };
    switch (name) {
      case 'expected_expenditure':
        //change expected expenditure
        expenditure_update[name] = parseInt(value);
        break;
      case 'category':
        expenditure_update[name] = parseInt(value);
        break;
      case 'date':
        expenditure_update[name] = value;
        break;
      default:
        expenditure_update[name] = value;
    }

    //updated
    if (name === 'category' || name === 'expected_expenditure') {
      const expectedExpenditure = expenditures.reduce(
        (prev, e) =>
          e.is_expected && e.id === expenditure_update.expected_expenditure
            ? e
            : prev,
        null
      );
      if (
        expectedExpenditure &&
        expectedExpenditure.category !== expenditure_update.category
      ) {
        if (name === 'category') {
          // check if category is coherent with expected_expenditure(EE) category, if not clear EE
          expenditure_update['expected_expenditure'] = 0;
        } else if (name === 'expected_expenditure') {
          // check if EE's category is coherent with category, if not set category to EE's category
          expenditure_update['category'] = expectedExpenditure.category;
        }
      }
    }

    setExpenditure(expenditure_update);
  };
  const handleSubmit = () => {
    const expToSubmit = {
      ...expenditure,
      expected_expenditure:
        expenditure.expected_expenditure === 0
          ? null
          : expenditure.expected_expenditure,
      category: expenditure.category === 0 ? null : expenditure.category,
    };
    if ('id' in expenditure) {
      setIsLoaded(false);
      APIEditExpenditure(
        authToken,
        expToSubmit,
        (data) => {
          //success
          setExpenditures(
            expenditures.map((e) => (e.id === data.id ? data : e))
          );
          // run second API call to update prospect-related informations
          setShowExpenditureModal(false);
        },
        (error) => {
          //error
          console.log('error while adding new expenditure: ', error);
        },
        () => {
          setIsLoaded(true);
        }
      );
    } else {
      setIsLoaded(false);
      APIAddExpenditure(
        authToken,
        expToSubmit,
        (data) => {
          //success
          setExpenditures([...expenditures, data]);
          setShowExpenditureModal(false);
          // run second API call to update prospect-related informations
        },
        (error) => {
          //error
          console.log('error while adding new expenditure: ', error);
        },
        () => {
          setIsLoaded(true);
        }
      );
    }
  };
  const getExpectedExpendituresOrderedByCategoryId = () => {
    return expenditures
      .filter((e) => e.is_expected)
      .sort((a, b) => {
        return a.category > b.category ? 1 : a.category < b.category ? -1 : 0;
      });
  };

  const getTitle = () => {
    if ('id' in expenditure) {
      if (expenditure.is_expected) {
        return 'Edit expected expenditure';
      } else {
        return 'Edit actual expenditure';
      }
    } else {
      if (expenditure.is_expected) {
        return 'Add expected expenditure';
      } else {
        return 'Add actual expenditure';
      }
    }
  };

  return (
    <Modal
      show={props.show}
      onHide={() => {
        setShowExpenditureModal(false);
      }}
      size='xl'
    >
      <Modal.Header closeButton>
        <Modal.Title>{getTitle()}</Modal.Title>
      </Modal.Header>
      <Modal.Body>
        <div>
          <form onSubmit={handleSubmit}>
            <div className='form-group'>
              <p>
                <label htmlFor='name'>Description</label>
                <input
                  type='text'
                  className='form-control'
                  name='name'
                  placeholder='Description'
                  value={expenditure.name}
                  onChange={handleChange}
                />
              </p>
              <p>
                <label htmlFor='name'>Value</label>
                <input
                  type='number'
                  step={0.01}
                  className='form-control'
                  name='value'
                  placeholder=''
                  value={
                    parseFloat(expenditure.value)
                      ? parseFloat(expenditure.value)
                      : ''
                  }
                  onChange={handleChange}
                />
              </p>
              <p>
                <label htmlFor='name'>{'Date & Time'}</label>
                <input
                  type='datetime-local'
                  className='form-control'
                  name='date'
                  placeholder='Date & Time'
                  value={expenditure.date}
                  onChange={handleChange}
                />
              </p>
              <p>
                <label htmlFor='category'>Category</label>
                <select
                  name='category'
                  className='form-control'
                  onChange={handleChange}
                  value={expenditure.category ? expenditure.category : 0}
                >
                  <option key={'categorydefault0'} value={0}>
                    -----
                  </option>
                  {workingDB.categories.map((c) => (
                    <option key={'category' + c.id} value={c.id}>
                      {c.name}
                    </option>
                  ))}
                </select>
              </p>
              {!expenditure.is_expected ? (
                <p>
                  <label htmlFor='expected_expenditure'>
                    Expected expenditure
                  </label>
                  <select
                    name='expected_expenditure'
                    className='form-control'
                    onChange={handleChange}
                    value={
                      expenditure.expected_expenditure
                        ? expenditure.expected_expenditure
                        : 0
                    }
                  >
                    <option key={'expectedexpendituredefault0'} value={0}>
                      -----
                    </option>
                    {getExpectedExpendituresOrderedByCategoryId().map((e) => (
                      <option key={'expectedexpenditure' + e.id} value={e.id}>
                        {e.value.toString() + ', ' + e.name}
                      </option>
                    ))}
                  </select>
                </p>
              ) : null}
            </div>
          </form>
        </div>
      </Modal.Body>
      <Modal.Footer>
        <div className='text-center w-100'>
          <button
            className={'btn btn-primary px-5'}
            onClick={handleSubmit}
            disabled={!isLoaded}
          >
            {'Save'}
          </button>
        </div>
      </Modal.Footer>
    </Modal>
  );
};

export default AddEditExpenditureModal;
