import React from 'react';
import { InlineIcon } from '@iconify/react';
import sync16 from '@iconify-icons/octicon/sync-16';

const RefreshBtn = (props) => {
  const { isLoaded, onRefresh } = props;
  const [justSynced, setJustSynced] = React.useState(isLoaded);
  React.useEffect(() => {
    setJustSynced(isLoaded);
    setTimeout(() => {
      setJustSynced(false);
    }, process.env.REACT_APP_ALERT_DEFAULT_DURATION);
  }, [isLoaded]);
  return (
    <button
      className='mirror btn mr-1 btn-outline-primary'
      onClick={onRefresh}
      disabled={!isLoaded}
    >
      <InlineIcon icon={sync16} className={justSynced ? 'spin' : ''} />
    </button>
  );
};
export default RefreshBtn;
