import React from 'react';

const CategoryCell = (props) => {
  const { category } = props;
  const { name } = category;
  return (
    <div className='w-100 pl-1'>
      <div className='text-primary m-1 clearfix'>
        <span>{name}</span>
      </div>
    </div>
  );
};

const Prospect = (props) => {
  const { workingDB } = props;
  const retrieveClassForDeltaDiv = (value) => {
    if (value > 0) {
      return 'text-success';
    } else if (value < 0) {
      return 'text-danger';
    }
    return 'text-dark';
  };
  return (
    <React.Fragment>
      <div key='prospectCell' className='border border-primary rounded my-2'>
        <div className='w-100 text'>
          <div className='text-primary m-1 border-primary rounded bg-primary pl-1'>
            <div className='text-light'>Overall prospect</div>
          </div>
        </div>
        <div className='clearfix m-1 text-center'>
          <div className='float-left text-justify'>
            <span className='text-muted'>
              <small>
                <em>Income</em>
              </small>
              <br />
            </span>
            {workingDB.income}
          </div>
          <div className='float-right text-right'>
            <span className='text-muted'>
              <small>
                <em>Current money</em>
              </small>
              <br />
            </span>
            {workingDB.current_money.value}
          </div>
        </div>
        <div className='clearfix m-1 text-center'>
          <div className='float-left text-justify'>
            <span className='text-muted'>
              <small>
                <em>Expected expenditure</em>
              </small>
              <br />
            </span>
            {workingDB.expected_expenditure}
          </div>
          <div className='float-right text-right'>
            <span className='text-muted'>
              <small>
                <em>Actual expenditure</em>
              </small>
              <br />
            </span>
            {workingDB.actual_expenditure}
          </div>
          <div className=''>
            <span className='text-muted'>
              <small>
                <em>Delta</em>
              </small>
              <br />
            </span>
            <div
              className={retrieveClassForDeltaDiv(workingDB.delta_expenditure)}
            >
              {workingDB.delta_expenditure}
            </div>
          </div>
        </div>
        <div className='clearfix m-1 text-center'>
          <div className='float-left text-justify'>
            <span className='text-muted'>
              <small>
                <em>Expected saving</em>
              </small>
              <br />
            </span>
            {workingDB.expected_saving}
          </div>
          <div className='float-right text-right'>
            <span className='text-muted'>
              <small>
                <em>Actual saving</em>
              </small>
              <br />
            </span>
            {workingDB.actual_saving}
          </div>
          <div className=''>
            <span className='text-muted'>
              <small>
                <em>Delta</em>
              </small>
              <br />
            </span>
            <div className={retrieveClassForDeltaDiv(workingDB.delta_saving)}>
              {workingDB.delta_saving}
            </div>
          </div>
        </div>
      </div>
      {workingDB.categories.length === 0 && (
        <p className='text-center'>
          <em>
            No categories created yet.
            <br />
            Go to actual or expected expenditure panel and create one.
          </em>
        </p>
      )}
      {workingDB.categories.map((category) => (
        <div
          key={'categoryprospect' + category.id}
          className='border border-primary rounded my-2 pb-1'
        >
          <CategoryCell category={category} showEditFunctions={false} />
          <div className='clearfix m-1 text-center'>
            <div className='float-left text-justify'>
              <span className='text-muted'>
                <small>
                  <em>Expected</em>
                </small>
                <br />
              </span>
              {category.expected_expenditure}
            </div>
            <div className='float-right text-right'>
              <span className='text-muted'>
                <small>
                  <em>Actual</em>
                </small>
                <br />
              </span>
              {category.actual_expenditure}
            </div>
            <div className=''>
              <span className='text-muted'>
                <small>
                  <em>Delta</em>
                </small>
                <br />
              </span>
              <div className={retrieveClassForDeltaDiv(category.delta)}>
                {category.delta}
              </div>
            </div>
          </div>
        </div>
      ))}
    </React.Fragment>
  );
};

export default Prospect;
