import React from 'react';
import Alert from 'react-bootstrap/Alert';

const Alerts = (props) => {
  const { alerts, onClose } = props;
  return (
    <div>
      {alerts.length > 0 &&
        alerts.map((alert) => (
          <Alert
            key={'alert' + alert.id}
            variant={alert.type}
            className='mb-1'
            onClose={() => {
              onClose(alert);
            }}
            dismissible
          >
            {alert.body}
          </Alert>
        ))}
    </div>
  );
};

export default Alerts;
